<template>
  <div class="smangent">
    <!-- 查询 -->
    <el-card>
      <el-form :inline="true" :model="queryfrom" class="me_from demo-form-inline">
        <el-form-item>
          <el-input v-model="queryfrom.departmentName" placeholder="根据门店名字或门店ID查询"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" style="background:#0b8d7e;border:none;" @click="Search">搜索</el-button>
          <!-- <el-button type="primary" style="background:#f1420ef7;border:none;" @click="AddPic">添加开票</el-button> -->
        </el-form-item>
      </el-form>
    </el-card>
    <!-- 查询 -->
    <!-- 表格数据 -->
    <el-card style="margin-top:20px;">
      <header style="font-weight: 800;padding-bottom: 8px;color: #000;">门店管理列表</header>
      <el-table
        ref="table"
        height="410"
        :cell-style="cellStyle"
        :header-cell-style="{background:'#edfaf8',color:'#333333'}"
        :row-class-name="tableRowClassName"
        :data="tableData"
        border
        style="width: 100%"
      >
        <el-table-column prop="departmentId" label="门店ID" width="100"></el-table-column>
        <el-table-column prop="departmentName" width="180" label="门店"></el-table-column>
        <el-table-column prop="expandUserName" label="第一拓展人"></el-table-column>
        <el-table-column prop="level" label="门店级别"></el-table-column>
        <el-table-column fixed="right" label="操作" width="100">
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small">详情</el-button>
          </template>
        </el-table-column>
      </el-table>
      <Pagination
        @handleSizeChange="handleSizeChange"
        @handleCurrentChange="handleCurrentChange"
        :paginationData="paginationData"
      />
    </el-card>
    <!-- 表格数据 -->
  </div>
</template>
<script>
import { deptList } from "@/api/business/smangent";
import myMixin from '@/mixin/index'
export default {
    mixins:[myMixin],
  data() {
    return {
      queryfrom: { //主页数据参数查询
        departmentName: "",
        pageSize: 15,
        pageNo: 1
      },
      tableData:[],
       paginationData:{
                  pageSize:15,
                  currentPage:1,
                  total:100
       },
    };
  },
  methods: {
    //   搜索
    Search() {
      this.requestList();
    },
    // 数据请求
    requestList() {
      deptList(this.queryfrom).then(res => {
        console.log(res);
        if(res.data.code==='000'){
            this.tableData=res.data.bizData.items
        }
      });
    },
    handleSizeChange(val){},
    handleCurrentChange(val){},
  },
  mounted() {
    this.requestList();
  }
};
</script>
<style scoped>
</style>